<template>
  <div>
    <div class="p3">
      <a class="tertiary font-title" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>EXIT</a>
    </div>

    <div class="p3">
      <div class="m4-bottom">
        <div class="flex-row button alt" @click="$router.push('/disclosures')">
          <i class="fas fa-exclamation-circle m1-right"></i>
          <div>FILL OUT DISCLOSURES</div>
          <div class="text-right flex-one p1-right"><i class="fas fa-chevron-right"></i></div>
        </div>
      </div>

      <div class="m4-bottom">
        <title-label title="APPROVE / DECLINE"></title-label>
        <div v-for="(appointment, i) in pending" :key="i">appt</div>
        <p v-if="pending.length === 0" class="tertiary">No pending showings</p>
      </div>

      <div class="m4-bottom">
        <title-label title="SCHEDULED"></title-label>
        <div v-for="(appointment, i) in scheduled" :key="i">appt</div>
        <p v-if="scheduled.length === 0" class="tertiary">No scheduled showings</p>
      </div>

      <div v-if="completed.length > 0" class="m4-bottom">
        <title-label title="COMPLETED"></title-label>
        <div v-for="(appointment, i) in completed" :key="i">appt</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'

export default {
  name: 'Client',
  components: { TitleLabel },
  data () {
    return {
      pending: [],
      scheduled: [],
      completed: []
    }
  },
  inject: ['userClient'],
  computed: {
    ...mapGetters(['session'])
  },
  methods: {
    ...mapActions(['setSession']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    }
  },
  mounted () {
    const self = this
    this.userClient.getAppointments(this.session)
      .then(appointments => {
        self.pending = appointments.pending
        self.scheduled = appointments.scheduled
        self.completed = appointments.completed
      })
  }
}
</script>
