import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    session: undefined
  },
  getters: {
    session: state => state.session
  },
  mutations: {
    SET_SESSION (state, session) {
      state.session = session
    }
  },
  actions: {
    setSession ({ commit }, session) {
      commit('SET_SESSION', session)
    }
  }
})
